.List{
    min-height: 100vh;
        display: grid;
        max-width: calc(100vw - 48px);
        grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
        grid-gap: 16px;
        margin-top: 90px;
        margin-bottom: 0;
        padding: 0;
        list-style: none;
        margin-left: auto;
        margin-right: auto;
        padding: 50px 80px 50px;
}